h1{
    color: tomato;
    text-shadow: 
    -1px -1px 0 firebrick,
    -2px -2px 0 firebrick,
    -3px -3px 0 firebrick,
    -4px -4px 0 firebrick,
    -5px -5px 0 firebrick,
    -6px -6px 0 firebrick,
    -7px -7px 0 firebrick,
    -8px -8px 0 firebrick,
    -30px 20px 40px dimgrey;
    transition: all 0.5s;
    transform: scale(1.4);
}

h1:hover{
    transform: RotateZ(-13deg);
}
#btn{
    background:linear-gradient(to right, rgb(37, 0, 138), rgb(255, 0, 0));
    transition: all 1s;
    opacity: 0.7;
}
#btn:hover{
    transform:scale(1.4);
    background:linear-gradient(to right, rgb(255, 0, 0), rgb(0, 30, 255));
}